import React from 'react';

const Register = ({OnRouteChange}) => {
    return (
        <article className="br3 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 center">
        <main className="pa4 black-80 center">
        <form className="measure">
          <fieldset id="sign_up" class="ba b--transparent ph0 mh0">
            <legend className="f4 fw6 ph0 mh0">Register</legend>
            <div className="mt3">
              <label className="db fw6 lh-copy f6" for="email-address">Name</label>
              <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="text" name="name"  id="namexxxxx" />
            </div>
            <div className="mt3">
              <label className="db fw6 lh-copy f6" for="email-address">Email</label>
              <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address" />
            </div>
            <div className="mv3">
              <label className="db fw6 lh-copy f6" for="password">Password</label>
              <input className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password"  />
            </div>
          </fieldset>
          <div className="">
            <input
            onClick = {()=>OnRouteChange("home")}
             className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Register" />
          </div>
          <div className="lh-copy mt3">
            <p
            onClick={()=>OnRouteChange('signin')}
             className="f6 link dim black db pointer">Sign In</p>
          </div>
        </form>
      </main>
      </article>
    )
}

export default Register;